"use client";
import TextGenerator from "@/components/TextGenerator";
import { useEffect, useState } from "react";

const GridComponent = () => {
  const gridRows = 10;
  const gridCols = 18;
  const [cards, setCards] = useState([]);

  const cardTypes = [
    { className: "card-7x4", width: 7, height: 4, count: 1, data: "Portfolio" },
    { className: "card-3x3", width: 3, height: 3, count: 7, data: "High Tier weight carrying things" },
    { className: "card-2x2", width: 2, height: 2, count: 150, data: "Experiences" },
    { className: "card-1x1", width: 1, height: 1, count: 200, data: "general stuff" },
  ];

  const colors = [
    { primary: "#282828", secondary: true },
    { primary: "#F8F3EC", secondary: false },
    { primary: "#FF5E54", secondary: true },
    { primary: "#2BBF5D", secondary: true },
    { primary: "#1283EB", secondary: true },
    { primary: "#FFE459", secondary: false },
    { primary: "#7B61FF", secondary: true },
  ];

  const store = [];

  const isOccupied = (x, y, width, height) => {
    return store.some(
      (card) =>
        x < card.x + card.width &&
        x + width > card.x &&
        y < card.y + card.height &&
        y + height > card.y
    );
  };

  const chooseRandomPosition = (width, height) => {
    let x, y;
    let attempts = 100;
    do {
      x = Math.floor(Math.random() * (gridCols - width + 1));
      y = Math.floor(Math.random() * (gridRows - height + 1));
    } while (isOccupied(x, y, width, height) && --attempts > 0);

    return attempts > 0 ? { x, y } : null;
  };

  useEffect(() => {
    const placeCards = async () => {
      const newCards = [];
      for (const { className, width, height, count, data } of cardTypes) {
        for (let i = 0; i < count; i++) {
          const position = chooseRandomPosition(width, height);
          if (position) {
            const chosenColor = colors[Math.floor(Math.random() * colors.length)];
            store.push({ ...position, width, height });

            newCards.push({
              className,
              ...position,
              width,
              height,
              data,
              color: chosenColor.primary,
              textColor: chosenColor.secondary ? "white" : "#282828",
            });

            setCards([...newCards]);
          }
        }
      }
    };

    placeCards();
  }, []);

  return (
    <div
      className="grid gap-1 bg-stone-900 p-4 w-full h-full place-content-center"
      style={{
        gridTemplateColumns: `repeat(${gridCols}, 100px)`,
        gridTemplateRows: `repeat(${gridRows}, 100px)`,
        display: "grid",
      }}
    >
      {cards.map((card, index) => (
        <div
          key={index}
          className="flex items-center justify-center font-bold transition-all duration-300 ease-in-out text-pretty transition-all"
          style={{
            gridColumn: `${card.x + 1} / span ${card.width}`,
            gridRow: `${card.y + 1} / span ${card.height}`,
            backgroundColor: card.color,
            color: card.textColor,
            border: card.className === "card-7x4" ? "0px solid #ffffff" : "none",
            borderRadius: card.className === "card-7x4" ? "70px" : "1px",
            fontSize: card.className === "card-7x4" ? "7rem" : "1.3rem",
            fontFamily: "Poppins, sans-serif",
          }}
        >
          <TextGenerator data={card?.data} size={"small"} />

        </div>
      ))}
    </div>
  );
};

export default GridComponent;
